<template lang="html">
  <div class="business-circle-page">
    <!-- <business-circle-head :circles="circles" :title="title"
      @select-circle="selectCircle"
    ></business-circle-head> -->
    <default-head header="商业圈"></default-head>
    <div class="">
      <!-- <circle-message></circle-message>
      <circle-message></circle-message>
      <circle-message></circle-message> -->
      <no-data></no-data>
    </div>

    <!-- <tabbar selected="/businesscircle"></tabbar> -->
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import noData from '@/components/common/noData'
import circleMessage from '@/components/businessCircle/circleMessage'
import tabbar from '@/components/minCommon/tabbar'

export default {
  components: {defaultHead, circleMessage, tabbar, noData},
  data () {
    return {
      circles: ['商业圈', '同行圈', '国家政策', '国外同行', '国内动向'],
      title: '商业圈'
    }
  },
  methods: {
    selectCircle (item) {
      this.title = item
    }
  }
}
</script>

<style lang="less" scoped>
.business-circle-page {
  padding-top: .88rem;
}
</style>



