<template lang="html">
  <div class="circle-message">
    <div class="circle-message-left">
      <img src="../../assets/img/logo.png" alt="">
    </div>
    <div class="circle-message-right">
      <div class="name">柯南</div>
      <div class="msg">标注表示地图上的点。API提供了默认图标样式，您也可以通过Icon类来指定自定义图标。</div>
      <div class="foot">
        <span class="time">1小时前</span>
        <img src="../../assets/img/circle/NEWS@3x.png" alt="">
      </div>
    </div>
  </div>
</template>

<script>
export default {
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
  .circle-message {
    background: @whiteColor;
    border-bottom: 1px solid @lineColor;
    color: @defaultColor;
    font-size: .3rem;
    padding: .2rem .22rem;
    overflow: hidden;
    .circle-message-left {
      width: .9rem;
      margin-right: .2rem;
      float: left;
      img {
        width: 100%;
        height: .9rem;
      }
    }
    .circle-message-right {
      overflow: hidden;
      .name {
        font-size: .32rem;
        color: @activeColor;
      }
      .msg {
        margin-top: .2rem;
        line-height: .4rem;
      }
      .foot {
        overflow: hidden;
        margin-top: .3rem;
        .time {
          float: left;
          color: @lightColor;
          font-size: .28rem;
        }
        img {
          float: right;
          width: .48rem;
          height: .4rem;
        }
      }
    }
  }
</style>



